import React,{Component} from 'react';
import Slider from 'react-slick';
import PropTypes from 'prop-types';
import './PosterSlide.css';


class PosterSlide extends Component{
  render() {
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay:true,
      className: "posterSlide",
      dotsClass : 'posterSlide__dots'
    };
    let data = this.props.data;
    let images = data.map((val,index)=>{
      return <div key={val.id}>
        <img className="posterSlide__image" src={val.src} alt={index}/>
      </div>
    })
    return (
      <Slider {...settings}>
        {images}
      </Slider>
    );
  }
}
PosterSlide.propType = {
  data : PropTypes.array.isRequired
}

export default PosterSlide;



