import React,{Component} from 'react';
import Stars from './Stars';
import './ScoreDistribute.css';

class ScoreDistribute extends Component{
  render(){
    return (
      <div className="scoreDistribute">
        <div className="score">
          <div className="score__value">9.3</div>
          <div className="score__desc">10000人评</div>
        </div>
        <div className="distribute">
          <div className="distribute__item">
            <Stars score="10"/>
            <div className="distribute__bottom">
              <span className="distribute__top" style={{width:"90%"}}/>
            </div>
          </div>
          <div className="distribute__item">
            <Stars score="8"/>
            <div className="distribute__bottom">
              <span className="distribute__top"  style={{width:"70%"}}/>
            </div>
          </div>
          <div className="distribute__item">
            <Stars score="6"/>
            <div className="distribute__bottom">
              <span className="distribute__top"  style={{width:"80%"}}/>
            </div>
          </div>
          <div className="distribute__item">
            <Stars score="4"/>
            <div className="distribute__bottom">
              <span className="distribute__top"  style={{width:"40%"}}/>
            </div>
          </div>
          <div className="distribute__item">
            <Stars score="2"/>
            <div className="distribute__bottom">
              <span className="distribute__top"  style={{width:"50%"}}/>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default ScoreDistribute;



