import React,{Component} from 'react';
import './BaseInfo.css';

class BaseInfo extends Component{
  render(){
    return (
      <div className="baseInfo">
        <div className="baseInfo__detail">
          <ul>
            <li><h3 className="baseInfo__name">泰坦</h3></li>
            <li className="baseInfo__subTitle">Titan</li>
            <li className="baseInfo__other">剧情 / 科幻</li>
            <li className="baseInfo__other">美国 | 120分钟</li>
            <li className="baseInfo__other">2017-01-19 在中国大陆上映</li>
          </ul>
        </div>
        <div className="baseInfo__poster" onClick={this.props.showImage} style={{backgroundImage:"url(/source/movie/333.jpg)"}}>
        </div>
      </div>
    )
  }
}
export default BaseInfo;



