import React from 'react';
import Stars from './Stars';
import './ScoreSummary.css';

const ScoreSummary = () => {
  return (
    <div className="scoreSummary">
      <div className="scoreSummary__top">
        <div className="summaryItem">
          <div className="summaryItem__value">9.3<Stars score="9.3"/></div>
          <div className="summaryItem__title">观众评分人数: 221878</div>
        </div>
        <div className="summaryItem">
          <div className="summaryItem__value">99%</div>
          <div className="summaryItem__title">V淘推荐度</div>
        </div>
        <div className="summaryItem">
          <div className="summaryItem__value">351878</div>
          <div className="summaryItem__title">想看人数</div>
        </div>
      </div>
      <div className="summaryBtn">
        <button className="summaryBtn__item summaryBtn__item--want"><i/><span>想看</span></button>
        <button className="summaryBtn__item summaryBtn__item--see"><i/><span>看过</span></button>
      </div>
    </div>
  )
};

export default ScoreSummary;



