import React,{Component} from 'react';
import './MovieItem.css';

class MovieItem extends  Component{
  render(){
    return (
      <div className="moveItem">
        <div className="moveItem__poster">
          <img src="./source/movie/333.jpg" alt=""/>
        </div>
        <div className="moveItem__details">
          <div className="moveItem__name">泰坦</div>
          <div className="moveItem__score">观众评分 <span>8.1</span></div>
          <div className="moveItem__director">导演：杨鹏</div>
          <div className="moveItem__actor">演员：杨鹏</div>
          <div className="moveItem__tag">
            <span className="tTag tTag--red">今日最热</span>
            <span className="tTag tTag--bule">口碑最佳</span>
          </div>
        </div>
        <div className="moveItem__btn">
          <button className="tBtn">购票</button>
          <span className="moveItem__price">今日购票，9.9￥起</span>
        </div>
      </div>
    )
  }
}
export default MovieItem;



