import React,{Component} from 'react';
import ReactDOM from 'react-dom';
import './FoldableText.css';

class FoldableText extends Component{
  state = {
    isFold : false,
    isNeedFold : false
  };
  componentDidMount(){
    const dom = ReactDOM.findDOMNode(this);
    if (dom.clientHeight > 28*3) {
      this.setState({
        isFold : true,
        isNeedFold : true
      })
    }
  }
  toggleStatus = () => {
    let prevFold = this.state.isFold;
    if (this.state.isNeedFold){
        this.setState({
          isFold : !prevFold
        })
    }
  };
  render(){
    let {isFold} = this.state;
    return (
      <div className={`foldableText ${isFold ? 'foldableText--fold':''}`} onClick={this.toggleStatus}>
        {this.props.children}
        {isFold && <div className="foldableText__label">展开</div>}
      </div>
    )
  }
}
export default FoldableText;



