import React,{Component} from 'react';
import TopBar from './components/TopBar.jsx';
import PosterSlide from './components/PosterSlide.jsx';
import MovieItem from './components/MovieItem.jsx';
import TabMenu from '../../components/TabMenu.jsx';
import RenderToBody from '../../components/RenderToBody.jsx';
import CityLayer from './components/CityLayer.jsx';
import request from '../../helpers/request.js';
import {Link} from 'react-router-dom';
import './index.css';

class Home extends Component{
  state = {
    city : '成都',//当前城市
    poster : [],//slide 海报
    movie : [],//当前热映电影
    cityLayerVisible : false, //是否展示城市浮层页面
    current:'movie'
  }
  showCityLayer = () =>{
    this.setState({
      cityLayerVisible:true
    })
  };
  hidenCityLayer = () => {
    this.setState({
      cityLayerVisible:false
    })
  };
  componentWillMount(){
    this.getData();
  }
  getData = async () => {
   const data = await request('/index');
   return data;
  }
  data = [
    {
      id : 111,
      src : '/source/slider/slide1.jpeg'
    },
    {
      id : 222,
      src : '/source/slider/slide2.jpeg'
    },
    {
      id : 333,
      src : '/source/slider/slide3.jpeg'
    },
    {
      id : 444,
      src : '/source/slider/slide4.jpeg'
    },
    {
      id : 555,
      src : '/source/slider/slide5.jpeg'
    }
  ];

  render(){
    let {city,cityLayerVisible} = this.state;
    return (<div className="home">
      <TopBar city={city} showCityLayer={this.showCityLayer}/>
      <div className="home__slide">
        <div className="home__slideWrap">
          <PosterSlide data = {this.data}/>
        </div>
      </div>
      <ul className="home__content">
        <li><Link to='/detail'><MovieItem/></Link></li>
        <li><Link to='/detail'><MovieItem/></Link></li>
        <li><Link to='/detail'><MovieItem/></Link></li>
        <li><Link to='/detail'><MovieItem/></Link></li>
        <li><Link to='/detail'><MovieItem/></Link></li>
      </ul>
      <div className="home__menu">
        <TabMenu current='movie' hidenCityLayer={this.hidenCityLayer}/>
      </div>
      {cityLayerVisible && <RenderToBody><CityLayer hidenCityLayer={this.hidenCityLayer}/></RenderToBody>}

    </div>);
  }
}

export default Home;





