import React,{Component} from 'react';
import TabMenu from '../../components/TabMenu.jsx';
import LineLink from '../../components/LineLink.jsx';
import './User.css';
import '../../common.css';
class User extends Component{
  render(){
    return (
      <div className="user">
        <div className="user__top">
          <div className="user__operator tOperator">
            <div className="tOperator__icon tOperator__icon--setting"/>
            <div className="tOperator__icon tOperator__icon--message"/>
          </div>
          <div className="user__info">
            <div className="user__avatar" style={{
              background : "url(/source/avatar.jpg)",
              backgroundPosition: "-15px 0px"
            }}/>
            <div className="user__details">
              <div className="user__name">渣渣辉渣渣辉渣渣辉渣渣辉渣渣辉渣渣辉渣渣辉渣渣辉渣渣辉</div>
              <div>
                <span className="user__fellow">关注：10</span>
                <span className="user__fans">被关注：100</span>
              </div>
              <div className="user__level">黄金会员</div>
            </div>

          </div>
        </div>
        <div className="user__content">
          <LineLink title="收藏的电影" href="" extra="20"/>
          <LineLink title="看过的电影" href="" extra="20"/>
        </div>
        <div className="home__menu">
          <TabMenu current='user'/>
        </div>
      </div>
    )
  }
}
export default User;



