import React,{Component} from 'react';
import Slider from 'react-slick';
import './ImageSlide.css';
class ImgSlide extends Component{
  state = {
    index : 1
  }
  changeIndex = (index) => {
    this.setState({
      index : index + 1
    })
  }
  render(){
    let settings = {
      className : "imageSlide__content",
      afterChange : this.changeIndex
    }
    return (
      <div className="imageSlide" onClick={this.props.onClose}>
        <div className="imageSlide__index">
          {this.state.index} / 4
        </div>
        <Slider {...settings}>
          <div className="imageSlide__item">
            <img src="/source/slider/slide1.jpeg" alt=""/>
          </div>
          <div className="imageSlide__item">
            <img src="/source/slider/slide2.jpeg" alt=""/>
          </div>
          <div className="imageSlide__item">
          <img src="/source/slider/slide3.jpeg" alt=""/>
        </div>
          <div className="imageSlide__item">
            <img src="/source/slider/slide4.jpeg" alt=""/>
          </div>
        </Slider>
      </div>
    )
  }
}
export default ImgSlide;



