import React,{Component} from 'react';
import './SeatSelector.css';

const SEAT_WIDTH = 50;
const SEAT_HEIGHT = 50;
class SeatSelector extends Component{
  //获取影院座位数据
  getData(){
    fetch('https://www.easy-mock.com/mock/5b19f354b97c8172c435c369/seat')
      .then((res)=>res.json()).then((res)=>{
      this.seatInfo = res.data;
      this.drawSeat(this.seatInfo);
    });
  }
  // 组件挂载后
  componentDidMount(){
    let emptyImage  = new Image();
    let selectedImage = new Image();
    let soldImage = new Image();
    let count = 0;
    this.ctx = this.refs.canvas.getContext('2d');
    this.ctx.font = `${6 * devicePixelRatio}px Arial`;
    this.ctx.fillStyle = '#fff';
    this.ctx.textAlign = 'center';

    let loadCallback = () => {
      count ++;
      if (count === 3) {
        this.emptyImage = emptyImage;
        this.selectedIamge = selectedImage;
        this.soldImage = soldImage;
        this.getData();
      }
    }
    emptyImage.onload = loadCallback();
    selectedImage.onload = loadCallback();
    soldImage.onload = loadCallback();
    emptyImage.src = './source/seat-empty.png';
    selectedImage.src = './source/seat-selected.png';
    soldImage.src = './source/seat-sold.png';
  }
  // 组件状态更新后
  componentDidUpdate(){
    this.ctx.clearRect(0,0,500,300);
    this.drawSeat(this.seatInfo);
    this.drawSelectSeat();
  }
  drawSeat(data){
    let seatInfo = data;
    for (let i = 0; i < seatInfo.length; i++) {
      const {isSold,xPos,yPos} = seatInfo[i];
      const offsetLeft = (xPos - 1) * SEAT_WIDTH;
      const offsetTop = (yPos - 1) * SEAT_HEIGHT;
      if(isSold){
        //绘制已售样式
        this.ctx.drawImage(this.soldImage,offsetLeft,offsetTop,SEAT_WIDTH,SEAT_HEIGHT);
      }else {
        //绘制未售样式
        this.ctx.drawImage(this.emptyImage,offsetLeft,offsetTop,SEAT_WIDTH,SEAT_HEIGHT);
      }
    }
  }
  selectSeat=(e)=>{
    let pageX = e.pageX;
    let pageY = e.pageY;
    let {left,top} = this.refs.canvas.getBoundingClientRect()//获取canvas整个长度和宽度
    let xPos = Math.ceil((pageX - left) / SEAT_WIDTH);
    let yPos = Math.ceil((pageY - top) / SEAT_HEIGHT);
    let selectSeat = this.props.data;
    let seatInfo = this.seatInfo;
    let seat = seatInfo.find((item) =>{
      return item.xPos === xPos && item.yPos === yPos;
    })
    if (!seat && seat.isSold){
      return;
    }else {
      let seatIndex = selectSeat.findIndex(item => item.id === seat.id);//筛选已选中座位
      if (seatIndex > -1){
        // 如果选择的是已选中座位，那么取消该座位的选中状态
        this.props.onRemove(seat.id);
      }else {
        // 选择的座位是未选中状态，那么进行选中
        if (selectSeat.length >= 4){
          alert('不能超过四个座位');
        }else {
          this.props.onAdd(seat);
        }
      }
    }
  }
  //给当前选中的座位设置成选中状态的图片
  drawSelectSeat = () => {
    let selectSeat = this.props.data;
    // console.log('drawSelectSeat方法：',selectSeat)
    for (let i = 0; i < selectSeat.length; i ++){
      let {xPos,yPos,rowIndex,colIndex} = selectSeat[i];
      const offsetLeft = (xPos - 1) * SEAT_WIDTH;
      const offsetTop = (yPos - 1) * SEAT_HEIGHT;
      this.ctx.drawImage(this.selectedIamge,offsetLeft,offsetTop,SEAT_WIDTH,SEAT_HEIGHT);
      this.ctx.fillText(`${rowIndex}排`,offsetLeft + SEAT_WIDTH/2,offsetTop + SEAT_HEIGHT/2.4);
      this.ctx.fillText(`${colIndex}座`,offsetLeft + SEAT_WIDTH/2,offsetTop + SEAT_HEIGHT*2/2.8);
    }
  }
  render(){
      return (
          <canvas style={{width:500,height:300}} ref='canvas' width={500} height={300} onClick={this.selectSeat}></canvas>
      )
    }
}
export default SeatSelector;



