import React,{Component} from 'react';
import MovieInfo from './components/MovieInfo';
import SeatSelected from './components/SeatSelected';
import SeatSelector from './components/SeatSelector';
import './Seat.css';
import '../../common.css';

class Seat extends Component{
  state = {
    selectSeat : []
  }
  addSeat = (seat) =>{
    let {selectSeat} = this.state;
    selectSeat.push(seat);
    this.setState({selectSeat});
  }
  removeSeat = (id) => {
    this.setState({selectSeat:this.state.selectSeat.filter(seat=>seat.id !== id )});
  }
  render(){
    let {selectSeat} = this.state;
    return (
      <div className="seat">
        <div className="seat__top">
          <div className="tOperator">
            <div className="tOperator__icon tOperator__icon--back" onClick={()=>window.history.back()}/>
            <span className='tOperator__title'>万达影院</span>
            <div className="tOperator__icon tOperator__icon--share"/>
          </div>
        </div>
        <MovieInfo/>
        <div className='seat__main'>
          <div className="seat__tip"></div>
          <div className="seat__graph">
            <div className="seat__screen">B13屏幕</div>
            <div className='seat__wrap'>
              <SeatSelector data={ selectSeat} onAdd ={this.addSeat} onRemove = {this.removeSeat}/>
            </div>

          </div>
        </div>
        <SeatSelected data={selectSeat} onRemove = {this.removeSeat}/>
      </div>
    )
  }
}
export default Seat;



