import React,{Component} from 'react';
// import PropTypes from 'prop-types';
import './Stars.css';

class Stars extends Component{
  state = {
    pos : 0
  }
  componentWillMount(){
   let score  = parseInt(this.props.score,10);
   let pos = 0;
    switch (score){
      case 10 : pos = 0; break;
      case 9 : pos = 11; break;
      case 8 : pos = 22; break;
      case 7 : pos = 33; break;
      case 6 : pos = 44; break;
      case 5 : pos = 55; break;
      case 4 : pos = 66; break;
      case 3 : pos = 77; break;
      case 2 : pos = 88; break;
      case 1 : pos = 99; break;
      case 0 : pos = 111; break;
      default : pos = 111; break;
    }
    this.setState({pos})
  }
  render(){
    let {pos} = this.state;
    return (
      <div className="stars" style={{backgroundPosition : `center -${pos}px`}}>
        {/*<div className="stars__top"></div>*/}
      </div>
    )
  }
}
// Stars.propTypes = {
//   value : PropTypes.number.isRequired,
//   size : PropTypes.number.isRequired
// }
export default Stars;



