export default async function (path,option) {
    const url = 'https://www.easy-mock.com/mock/5b19f354b97c8172c435c369' + path;
    const opt = Object.assign({method:'get',},option);
    try {
      const response = await fetch(url,opt);
      let {data,status} = await response.json();
      if (1){
        return data;
        // console.log(data);
      } else {
        console.log(`status: ${status}`)
      }
    }catch (e) {
      // console.log(e);
    }
}



