import React,{Component} from 'react';
import './CityLater.css';
class CityLayer extends Component{
  render(){
    return (
      <div className="cityLayer">
          <div className="cityLayer__title">
              <div className="cityLayer__close" onClick={this.props.hidenCityLayer}>关闭</div>
             <span>选择城市</span>
          </div>
        <div className="cityLayer__content">
          <div className="cityBlock">
            <div className="cityBlock__label" id="定位">定位城市</div>
            <ul className="cityBlock__wrap">
              <li className="cityBlock__item">成都</li>
            </ul>
          </div>
          <div className="cityBlock">
            <div className="cityBlock__label" id="热门">热门城市</div>
            <ul className="cityBlock__wrap">
              <li className="cityBlock__item">成都</li>
              <li className="cityBlock__item">上海</li>
              <li className="cityBlock__item">北京</li>
              <li className="cityBlock__item">重庆</li>
              <li className="cityBlock__item">广州</li>
              <li className="cityBlock__item">深圳</li>
            </ul>
          </div>
          <div className="cityList">
            <div className="cityList__label" id="A">A</div>
            <ul className="cityList__wrap">
              <li className="cityList__item">啊霸</li>
              <li className="cityList__item">啊猫</li>
              <li className="cityList__item">啊狗</li>
              <li className="cityList__item">啊霸</li>
              <li className="cityList__item">啊猫</li>
              <li className="cityList__item">啊狗</li>
              <li className="cityList__item">啊霸</li>
              <li className="cityList__item">啊猫</li>
              <li className="cityList__item">啊狗</li>
              <li className="cityList__item">啊霸</li>
              <li className="cityList__item">啊猫</li>
              <li className="cityList__item">啊狗</li>
              <li className="cityList__item">啊霸</li>
              <li className="cityList__item">啊猫</li>
              <li className="cityList__item">啊狗</li>
              <li className="cityList__item">啊霸</li>
              <li className="cityList__item">啊猫</li>
              <li className="cityList__item">啊狗</li>
              <li className="cityList__item">啊霸</li>
              <li className="cityList__item">啊猫</li>
              <li className="cityList__item">啊狗</li>
              <li className="cityList__item">啊霸</li>
              <li className="cityList__item">啊猫</li>
              <li className="cityList__item">啊狗</li>
            </ul>
          </div>
        </div>
        <div className="cityLayer__index">
          <ul>
            <li><a href="#定位">定位</a></li>
            <li><a href="#热门">热门</a></li>
            <li><a href="#A">A</a></li>
          </ul>
        </div>
      </div>
    )
  }
}
export default CityLayer;



