import React,{Component} from 'react';
import PropTypes from 'prop-types';
import './LineLink.css';
class LineLink extends Component{
  render(){
    return (
      <a href={this.props.href} className="lineLink">
        <div className="lineLink__title">{this.props.title}</div>
        <div className="lineLink__extra">{this.props.extra}</div>
        <div className="lineLink__arrow"><i/></div>

      </a>
    )
  }
}
LineLink.propTypes = {
  href : PropTypes.string.isRequired,
  title:PropTypes.string.isRequired
}
export default LineLink;



