import React from 'react';
import './SeatSelected.css';

const SeatSelected = ({data,onRemove}) =>{
    return (

      <div className="seatSelected">
        <ul className="seatSelected__list">

          {
            data.map(item =>(
              <li className="seatSelected__item movieTicket" key={item.id}>
                <div className="movieTicket__detail">
                  <span className="movieTicket__postion">{item.rowIndex}排{item.colIndex}座</span>
                  <span className="movieTicket__price">33元</span>
                </div>
                <div className='movieTicket__close' onClick={()=>onRemove(item.id)}>x</div>
              </li>
            ))
          }
        </ul>
        <div className="seatSelected__buy">
          {data.length * 30}元 确认选座
        </div>
      </div>
    )
}

// SeatSelected.propTypes = {
//   data : PropTypes.array.isRequired
// }
export default SeatSelected;



